.Hero {
  height: 68vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-shadow: inset 0px 10px 7px -2px rgba(0, 0, 0, 0.15);
}

.Hero__title-container {
  padding-top: 5vh;
  padding-left: 5vw;
  display: flex;
  height: 100%;
}

.Hero__title {
  height: 200px;
}

.Hero__virtual-logo-container {
  height: 100%;
  display: flex;
  align-items: flex-end;
}

.Hero__virtual-logo {
  width: 12vw;
  margin: 2vw;
}

.Hero__virtual-logo-splash {
  width: 20vw;
  margin: 2vw;
}

@media only screen and (max-width: 600px) {
  .Hero {
    height: 80vh;
  }
  .Hero__title {
    height: 30%;
  }
  .Hero__virtual-logo-container {
    display: none;
  }
}